.menu-icon {
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  transition: opacity 150ms linear 0ms, color 150ms linear 0ms,
    -webkit-transform 400ms cubic-bezier(0.7, 0, 0.3, 1) 0ms;
  transition: transform 400ms cubic-bezier(0.7, 0, 0.3, 1) 0ms,
    opacity 150ms linear 0ms, color 150ms linear 0ms;
  transition: transform 400ms cubic-bezier(0.7, 0, 0.3, 1) 0ms,
    opacity 150ms linear 0ms, color 150ms linear 0ms,
    -webkit-transform 400ms cubic-bezier(0.7, 0, 0.3, 1) 0ms;
